import { HttpClient, HttpHeaders } from "@angular/common/http";
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { ConfigService } from './configure';

@Injectable({
   providedIn: 'root'
})

@Injectable()
export class UrlService {

   constructor(
      private config: ConfigService,
      private http: HttpClient
   ) {
   }

   createShortUrl(longUrl: string, shortID: string, expireType: string): Promise<any> {
      const url = `${this.config.host}${this.config.API.URL_SHORTEN}`;
      let reqData = {
         original_url: longUrl,
         short_key: shortID,
         expire_type: expireType,
         user_id: ''
      }
      const data = JSON.stringify(reqData)
      return this.http.post<any>(url, data, this.getRequestOptions()).toPromise();
   }

   getUrlDetail(id: string): Promise<any> {
      const url = `${this.config.host}${this.config.API.URL_DETAIL}`
                  .replace('{0}', id);
      return this.http.get<any>(url, this.getRequestOptions()).toPromise();
   }

   deleteUrl(id: string): Observable<any> {
      const url = `${this.config.host}${this.config.API.DELETE_URL}`
                  .replace('{0}', id);
      return this.http.delete(url, this.getRequestOptions());
   }

   updateShortUrl(id: string, alias: string): Promise<any> {
      const url = `${this.config.host}${this.config.API.UPDATE_URL}`
                  .replace('{0}', id)
                  .replace('{1}', alias);
      return this.http.post<any>(url, this.getRequestOptions()).toPromise();
   }

   getServiceStatus(): Promise<any> {
      const url = `${this.config.host}${this.config.API.SERVICE_STATUS}`;
      return this.http.get<any>(url, this.getRequestOptions()).toPromise();
   }

   getUrls(): Promise<any> {
      const url = `${this.config.host}${this.config.API.URL_LIST}`
                  .replace('{0}', this.config.userName);
      return this.http.get<any>(url, this.getRequestOptions()).toPromise();
   }

   checkSystemAdmin(): Observable<any> {
      const url = `${this.config.host}${this.config.API.CHECK_SYSTEM_ADMIN}`
                  .replace('{0}', this.config.userName);
      return this.http.get(url, this.getRequestOptions());
   }

//    protected getRequestOptions(httpOptions?: any): any {
//       httpOptions = { headers: new HttpHeaders() };
//       httpOptions.headers = {
//          'User': this.config.userName,
//          'Content-Type': 'application/json',
//          'Content-Security-Policy': 'upgrade-insecure-requests',
//          'Access-Control-Allow-Origin': '*'
//       };
//       return httpOptions;
//    }

   private getRequestOptions(): HttpOptions {
      const options: HttpOptions = new HttpOptions();
      let headers = new HttpHeaders();
      headers = headers.append(HttpHeader.CONTENT_TYPE, HttpHeader.APPLICATION_JSON);
      headers = headers.append(HttpHeader.CACHE_CONTROL, HttpHeader.NO_CACHE);
      headers = headers.append(HttpHeader.PRAGMA, HttpHeader.NO_CACHE);
      headers = headers.append(HttpHeader.EXPIRES, "Sat, 01 Jan 2000 00:00:00 GMT");
      options.headers = headers;
      return options;
   }
}

/**
 * Options passed
 */
export class HttpOptions {
   headers: HttpHeaders;
}

/**
 * Standardized and VMware-specific HTTP headers.
 */
export class HttpHeader {
   static readonly CONTENT_TYPE = "Content-Type";
   static readonly APPLICATION_JSON = "application/json";
   static readonly ACCEPT = "Accept";
   static readonly AUTHORIZATION = "Authorization";
   static readonly VMWARE_USER_HEADER_AUTHN = "vmware-use-header-authn";
   static readonly VAPI_HEADER_API_SESSION_ID = "vmware-api-session-id";
   static readonly VSPHERE_UI_XSRF_TOKEN: string = "X-VSPHERE-UI-XSRF-TOKEN";
   static readonly CACHE_CONTROL: string = "Cache-Control";
   static readonly NO_CACHE: string = "no-cache";
   static readonly PRAGMA: string = "Pragma";
   static readonly EXPIRES: string = "Expires";
   static readonly WEB_CLIENT_SESSION_ID: string = "webClientSessionId";

   static readonly SIGN_TOKEN = (tokenValue: string) => {
      return `SIGN token = "${tokenValue}"`;
   };
}
