import { Injectable } from '@angular/core';
import { Subject } from 'rxjs';
import { HttpClient } from '@angular/common/http';

interface API {
   URL_SHORTEN: string;
   URL_DETAIL: string;
   DELETE_URL: string;
   UPDATE_URL: string;
   URL_LIST: string;
   SERVICE_STATUS: string;
   CHECK_SYSTEM_ADMIN: string;
 }

@Injectable({
   providedIn: 'root'
})
export class ConfigService {
   subject: Subject<string> = new Subject<string>();
   obs$ = this.subject.asObservable();

   isSystemAdminSubject: Subject<string> = new Subject<string>();
   sysAdmin$ = this.isSystemAdminSubject.asObservable();

   API: API;

   // tslint:disable-next-line: variable-name
   private _username: string;
   public set userName(value: string) {
      this._username = value;
      this.subject.next(value);
   }
   public get userName(): string {
      return this._username;
   }

   private _isSystemAdmin;
   public get isSystemAdmin() {
      return this._isSystemAdmin;
   }
   public set isSystemAdmin(value) {
      this._isSystemAdmin = value;
      this.isSystemAdminSubject.next(value);
   }

   // tslint:disable-next-line: variable-name
   private _host: string;
   public set host(value: string) {
      this._host = value;
      this.subject.next(value);
   }
   public get host(): string {
      return this._host;
   }

   constructor(private http: HttpClient) {
      this.getConfig();
   }

   getConfig() {
      this.host = '';
      this.API = {
         URL_SHORTEN: '/api/shorten',
         URL_DETAIL: '/api/url/{0}',
         DELETE_URL: '/api/url/{0}',
         UPDATE_URL: '/api/url/{0}/{1}',
         URL_LIST: '/api/urls?user={0}',
         SERVICE_STATUS: '/api/service/status',
         CHECK_SYSTEM_ADMIN: '/api/service/admin/{0}'
      };
   }
}
