import { Component, OnInit, HostListener, ChangeDetectorRef } from '@angular/core';
import { Router, ActivatedRoute, RoutesRecognized } from '@angular/router';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Location } from '@angular/common';
import { ConfigService } from './service/configure';
import { UrlService } from './service/service';

@Component({
   selector: 'app-root',
   templateUrl: './app.component.html',
   styleUrls: ['./app.component.scss']
})

export class AppComponent implements OnInit {
   showDialog: boolean;
   userName: string;
   numberOfOverallUrl: number;
   redirectTimes: number;

   constructor(
      public router: Router,
      public activatedRoute: ActivatedRoute,
      public config: ConfigService,
      private locale: Location,
      private cdr: ChangeDetectorRef,
      private http: HttpClient,
      private service: UrlService,
   ) {
      localStorage.setItem('originalUrl', locale.path());
      if (localStorage.getItem('originalUrl')) {
         this.router.navigateByUrl(localStorage.getItem('originalUrl'));
      } else {
         if (locale.path() === '') {
            this.router.navigateByUrl('/shorten');
         }
      }
   }

   ngOnInit() {
      this.service.getServiceStatus().then(
         result => {
            this.numberOfOverallUrl = result['number_of_overall_urls'];
            this.redirectTimes = result['overall_redirect_times'];
         },
         error => {
            console.log(error['error']);
         }
      );
   }

   checkSystemAdmin() {
      if (this.config.userName && this.config.isSystemAdmin === undefined) {
         this.service.checkSystemAdmin().subscribe(result => {
            this.config.isSystemAdmin = true;
            console.log('isSystemAdmin = true');
         }, error => {
            this.config.isSystemAdmin = false;
            this.router.navigateByUrl('/shorten');
         });
      }
   }

   closeModal() {
      this.showDialog = false;
      this.config.userName = this.userName;
      this.setCookie('user', this.userName, 7);
      this.checkSystemAdmin();
   }

   @HostListener('window:keydown', ['$event'])
   handleKeyDown(event: KeyboardEvent) {
      if (this.showDialog && this.userName && event.key === 'Enter') {
         this.config.userName = this.userName;
         this.setCookie('user', this.userName, 7);
         this.showDialog = false;
      }
      this.cdr.detectChanges();
   }

   private setCookie(name: string, value: string, days: number) {
      let expires = "";
      if (days) {
         let date = new Date();
         date.setTime(date.getTime() + (days*24*60*60*1000));
         expires = "; expires=" + date.toUTCString();
      }
      document.cookie = name + "=" + (value || "")  + expires + "; path=/";
   }

   private getCookie(name: string) {
      let nameEQ = name + "=";
      let ca: Array<string> = document.cookie.split(';');
      let caLen: number = ca.length;
      for (let i=0; i < caLen; i++) {
         let c = ca[i];
         while (c.charAt(0)==' ') {
            c = c.substring(1,c.length);
         }
         if (c.indexOf(nameEQ) === 0) {
            return c.substring(nameEQ.length, c.length);
         }
      }
      return '';
   }

   private eraseCookie(name) {
      document.cookie = name +'=; Path=/; Expires=Thu, 01 Jan 1970 00:00:01 GMT;';
   }
}
